import styles from './styles/sideMenu.module.css'

function SideMenu ({menuClick, changeMenuClicked}) {
    return (
        <nav className={`${menuClick ? "transform translate-x-0 transition-all ease-out duration-500": "transform -translate-x-full transition-all ease-out duration-500 "} fixed w-full z-20 h-full bg-gray-200`}
            onClick={changeMenuClicked}
        >
            <div>
                {console.log("menu : ", menuClick ? "clicked ": "not clicked ")}
                <h1>Side Menu</h1>
            </div>

        </nav>
    )
}

export default SideMenu